import Scroll from '../../../base/scroll/scroll'
import Pay from '../../../base/pay/pay'
export default {
  name: "buyCoin",
  components: {
    Scroll,
    Pay
  },
  data() {
    return {
      paytype: 2,
      isChecked: 0,
      hasmoney: 25.65,
      title: '',
      moneyBox: [
        {
          price: '50',
          oldPrice: '50'
        },
        {
          price: '100',
          oldPrice: '10',
          info: '5'
        },
        {
          price: '200',
          oldPrice: '200',
          info: '16'
        },
        {
          price: '30',
          oldPrice: '300',
          info: '30'
        },
        {
          price: '500',
          oldPrice: '500',
          info: '75'
        },
      ],
      moneyIndex: 0,
      type: 1,
      chosed: true
    }
  },
  computed:{
    ataloCount:{
      get(){
        let ataloCount = 0
        let choosePrice = parseFloat(this.moneyBox[this.moneyIndex].price)
        ataloCount = choosePrice - (this.chosed?this.hasmoney:0)
        return ataloCount.toFixed(2)
      },
      set(val){
        return val
      }
    }
  },
  methods: {
    choosePay(index) {
      this.moneyIndex = index
    },
    isChosed(val) {
      this.chosed = val
    }
  }
}



